body {
  --pfe-reveal-duration: 0.1618s;
  --pfe-reveal-delay: 2s;
  transition: opacity var(--pfe-reveal-duration) ease-in-out;
  --pfe-broadcasted--text: var(--pfe-theme--color--text, #333);
  --pfe-broadcasted--link: var(--pfe-theme--color--link, #06c);
  --pfe-broadcasted--link--hover: var(--pfe-theme--color--link--hover, #003366);
  --pfe-broadcasted--link--focus: var(--pfe-theme--color--link--focus, #003366);
  --pfe-broadcasted--link--visited: var(--pfe-theme--color--link--visited, rebeccapurple);
  --pfe-broadcasted--link-decoration: none;
  --pfe-broadcasted--link-decoration--hover: underline;
  --pfe-broadcasted--link-decoration--focus: underline;
  --pfe-broadcasted--link-decoration--visited: none;
}

body[unresolved] {
  opacity: 0;
  animation: reveal var(--pfe-reveal-duration) var(--pfe-reveal-delay) 1 forwards;
}

@keyframes reveal {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
